import {Carousel, Col, Flex, Row} from "antd";
import header1 from "../assets/header1.jpg";
import header2 from "../assets/header2.jpg";
import header3 from "../assets/header3.jpg";
import header4 from "../assets/header4.jpg";
import rightCar from "../assets/headerCar.png";
import webIcon from "../assets/icon.png";
import {Typography} from "antd";
import useBreakpoint from "antd/es/grid/hooks/useBreakpoint";

const {Title, Text} = Typography;

const LandingView = () => {
    const screen = useBreakpoint();

    const data = [
        {
            bgImg: header1,
            title: "Welcome to Vintage Car Haven",
            info: "Explore our collection of classic automobiles from a bygone era."
        },

        {
            bgImg: header2,
            title: "Discover Timeless Beauty",
            info: "Step into the world of vintage cars and experience elegance and charm."
        },

        {
            bgImg: header3,
            title: "Preserving Automotive History",
            info: "Join us in celebrating the legacy of classic automobiles."
        },

        {
            bgImg: header4,
            title: "Embrace Nostalgia",
            info: "Relive the golden age of motoring with our curated selection of vintage cars."
        }
    ];

    return(
        <Carousel autoplay fade>
            {data.map((item, index) =>(
                <div key={index}>
                    <div style={{minHeight: 'calc(100vh - 64px)', backgroundImage: `url(${item.bgImg})`, backgroundPosition: "center", backgroundRepeat: "no-repeat", backgroundSize: "cover"}}>
                        <Row style={{display: "flex", alignItems: "center", justifyContent: 'center', minHeight: "inherit", maxWidth: '1600px', margin: '0 auto'}}>
                            <Col xs={24} lg={12} style={{backgroundColor: 'rgba(0,0,0,0.25)', padding: '4rem 2rem', borderRadius: '0.5rem', minHeight: (screen.lg ? "inherit" : screen.xs ? '50vh' : screen.sm ? '50vh' : screen.md ? '50vh' : "inherit"), display: "flex", alignItems: 'center', justifyContent: 'start'}}>
                                <Flex vertical style={{rowGap: (screen.lg ? '4rem' : screen.md ? "3rem" : screen.sm ? "2rem" : "1.5rem"), height: 'auto'}}>
                                    <img src={webIcon} alt={"Vintage Cars"} style={{height: '125px', width: '145px', margin: '0 auto'}}/>
                                    <Title style={{color: "white", fontWeight: 'bolder', fontSize: (screen.xxl ? "90px" : screen.xl ? "80px" : screen.lg ? "70px" : screen.md ? "70px" : screen.sm ? "60px" : "50px"), fontFamily: "Marshena", margin: 0}}>{item.title}</Title>
                                    <Text style={{color: "white", fontWeight: "600", fontSize: "18px"}}>{item.info}</Text>
                                </Flex>
                            </Col>
                            <Col xs={24} lg={12} style={{padding: '4rem 2rem',backgroundColor: 'rgba(255,255,255,0.25)', borderRadius: '0.5rem', minHeight: (screen.lg ? "inherit" : screen.xs ? '50vh' : screen.sm ? '50vh' : screen.md ? '50vh' : "inherit"), display: "flex", alignItems: "center", justifyContent: "center"}}>
                                <img style={{height: '50%', width: '75%', margin: '0 auto'}} src={rightCar} alt={"car"}/>
                            </Col>
                        </Row>
                    </div>
                </div>
            ))}
        </Carousel>
    )
}

export default LandingView;