import {Menu, Drawer, Button, Flex, Typography, Space} from "antd";
import {Header} from "antd/es/layout/layout";
import {useState} from "react";
import useBreakpoint from "antd/es/grid/hooks/useBreakpoint";
import {MenuOutlined} from "@ant-design/icons";
const {Title} = Typography;

const Navbar = () => {
    const [visible, setVisible] = useState(false);
    const screens = useBreakpoint();
    const showDrawer = () => {
        setVisible(true);
    };

    const onClose = () => {
        setVisible(false);
    };


    return (
        <Header>
            <Flex justify={"space-between"} align={"center"} style={{height: 'inherit', maxWidth: '1600px', margin: '0 auto'}}>
                <Space>
                    <img src={""} alt={""}/>
                    <Title level={3} style={{margin: 0, color: "white", fontFamily: 'Marshena'}}>Cars Project</Title>
                </Space>
                {screens.lg ? (<Menu theme="dark" mode="horizontal" defaultSelectedKeys={['1']}>
                    <Menu.Item key="1">Home</Menu.Item>
                    <Menu.Item key="2">About</Menu.Item>
                    <Menu.Item key="3">Services</Menu.Item>
                    <Menu.Item key="4">Contact</Menu.Item>
                </Menu>) : ( <>
                    <Button className="menuButton" type="primary" style={{borderRadius: '3px', backgroundColor: 'white', boxShadow: '0 0 5px rgba(255,255,255,0.5)'}} onClick={showDrawer}>
                        <MenuOutlined style={{color: 'black'}}/>
                    </Button>
                    <Drawer
                        title="Menu"
                        placement="right"
                        closable={false}
                        onClose={onClose}
                        open={visible}
                    >
                        <Menu mode="vertical" defaultSelectedKeys={['1']}>
                            <Menu.Item key="1">Home</Menu.Item>
                            <Menu.Item key="2">About</Menu.Item>
                            <Menu.Item key="3">Services</Menu.Item>
                            <Menu.Item key="4">Contact</Menu.Item>
                        </Menu>
                    </Drawer></>)}
            </Flex>
        </Header>
    )
}

export default Navbar;